.SocialLogin {
  display: flex;    
  flex-direction: column;
  align-items: center;
}

.SocialLogin .btn{
  display: inline-block;
  font-weight: 400;
  text-align: center;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  white-space: nowrap;
  padding: 0 10px;
  font-size: 14px;
  border-radius: 4px;
  height: 45px;
  line-height: 45px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all .3s cubic-bezier(.645,.045,.355,1);
  position: relative;
  /* color: rgba(0,0,0,.65); */
  color: black;
  background-color: transparent;
  /* border-color: #eee; */
  outline: none;
}

.SocialLogin .btn-block {
    display: block;
    max-width: 266px;
    min-width: 234px;
    background-color: #FDF01B;
  }

.SocialLogin .social-btn {
    margin-top: 70px;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 16px;
}

.SocialLogin .social-btn img {
    height: 32px;
    float: left;
    margin-top: 7px;
}

.SocialLogin .social-btn:after,
.SocialLogin .social-btn:before {
    content: '';
    display: block;
    position: absolute;
    left: 100%;
    width: 0;
    height: 0;
    border-style: solid;
}

.SocialLogin .social-btn:after {
    top: 8px;
    border-color: transparent transparent transparent #FDF01B;
    border-width: 10px;
}

.SocialLogin .social-btn:before {
    top: 7px;
    border-color: transparent transparent transparent #eee;
    border-width: 11px;
}

.SocialLogin #siba{
  border-radius: 14%;
  border: 2px solid rgb(51,51,51);;
}
