.BasicBoard{
    padding: 70px 0;
    min-height: 100vh;
    display: block;
}

.BasicBoard h3{
    margin: 0
}

.BasicBoard .basic-container{
    position: relative;
    margin: 0 auto;
    color: rgb(51,51,51);
    overflow: hidden;
    width: 85%;
    font-weight: 400;
}

.BasicBoard .basic-header{
    border-bottom: 1px solid #666;
    padding-bottom: 14px;
    margin-bottom: 15px;
}

.BasicBoard .hub-count{
    color: white;
    padding: 2px 6px; 
    border-radius: 9px;
    font-size: 14px;
    margin-right: 5px;
}

.BasicBoard .blue{
    background-color: #34BBEF;
}

.BasicBoard .red{
    background-color: #cc0000;
}

.BasicBoard .hub-add-btn{
    position: absolute;
    right: 0;
    top: 0;
    width: 103px;
    height: 25px;
    border-radius: 3px;
    border: none;
    cursor: pointer;
    font-size: 13px;
    padding: 0;
    display: flex;
    align-items: center;
    background-color: #ADD8E8;
}

.BasicBoard .hub-add-btn:focus{
    outline: none;
} 

.BasicBoard .hub-add-btn .ico{
    padding: 4px 0;
    border-radius: 3px 0 0 3px;
    background-color: #2A2C2B;
    color: white;
    width: 23%;
    height: 25px;
    z-index:2;
    position:relative;
    text-align: center;
}

.BasicBoard .hub-add-btn .ico:after{
    content:"";
    position:absolute;
    right:-6px;
    top: 5px;
    width: 0; 
    height: 0; 
    border-top: 7px solid transparent;
    border-bottom: 6px solid transparent;
    border-left: 6px solid #2A2C2B;
}

.BasicBoard .hub-add-btn .text{
    background-color: #ADD8E8;
    color: #2A2C2B;
    width: 77%;
    padding-left: 5px;
    font-size: 12px;
    text-align: center;
}
.HubBox{
    height: 192px;
    margin-bottom: 11px;
    border-radius: 2px;
    border: 1px solid rgb(234,234,234);
    display: flex;
    flex-direction: column;
    background-color: white;
}

.HubBox header{
    border-bottom: 1px solid rgb(234,234,234);
    padding: 0;
    display: flex;
    flex-direction: row;
}

.HubBox .hub-name{
    padding: 0;
    display: flex;
    flex-direction: column;
    width: 40%;
}

.HubBox .title{
    padding: 4px 0;
    font-size: 15px;
    margin: 3px 5px 0 10px;
    color: rgb(51,51,51);
}

.HubBox .subtitle{
    margin: 0 0 2px 31px;
    font-size: 11px;
    color: rgb(165,165,165);
}

.HubBox .hub-info{
    padding: 0;
    display: flex;
    flex-direction: row;
    width: 80%;
    align-items: center;
    justify-content: flex-end;
}

.HubBox .hub-info-item{
    margin-right: 10px;
}

.HubBox .hub-state{
    width: 25px;
    text-align: center;
    border-radius: 10px;
    padding: 1px 0 2px 0;
    font-size: 11px;
}

.HubBox .off{
    border: 1px solid rgb(255,86,64);
    color: rgb(255,86,64);
}

.HubBox .on{
    border: 1px solid green;
    color: green;
}

.HubBox section{
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 12px;
    padding: 8px 10px 0px 10px;
}

.HubBox article{
    display: flex;
    flex-direction: column;
    border-left: 3px solid #eee;
}

.HubBox .hub-row{
    display: flex;
    flex-direction: row;
    margin: 0  0 5px 0;
}

.HubBox .hub-label{
    width: 33%;
    color: #666666;
    white-space: pre;
    float: left;
    padding-left: 6px;
}

.HubBox .hub-content{
    text-align: right;
    width: 97%;
    font-weight: 500;
    color: #666666;
    white-space: pre;
    float: left;
}

.HubBox footer{
    margin-top: 6px;
    padding: 0px 10px 0px 10px;
    width: 100%;
}

.HubBox .wrap-btn{
    border-top: 1px solid #eee;
    position: relative;
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    padding-top: 7px;
}

.HubBox .short-info{
    position: absolute;
    left: 3px;
    top:6px;
    font-size: 11px;
    display: flex;
    border: 1px solid #eee;
    border-radius: 5px;
    padding: 3px 6px;
}

.HubBox .short-info-item{
    margin-right: 11px;
    vertical-align: center;
}

.HubBox .short-info-label{
    margin-left: 16px;
}

.HubBox .short-info-ico{
    position: absolute;
    top: 5px;
}
.InputContainer{
    padding: 0 0 17px;
    border-bottom: 1px solid;
    margin: 0 0 11px;
}

.InputContainer .input-cont-header h3{
    display: block;
    font-size: 15px;
    font-weight: bold;
    color: rgb(51,51,51);
    font-family: 'Noto Sans', sans-serif;
}

.InputContainer .desc{
    border: 1px solid #E4E4E4;
    border-radius: 3px;
    margin-top: 6px;
}

.InputContainer .desc-title{
    display: block;
    padding: 6px;
    font-weight: normal;
    font-size: 13px;
    line-height: 19px;
    color: rgb(46,172,188)
}

.InputContainer ul{
    margin: 0;
    padding: 0 0 7px 2px;
    position: relative;
    list-style: none;
}

.InputContainer li{
    margin: 0 0 5px 19px;
    color: rgb(51,51,51);
    display: list-item;
    list-style: none;
}

.InputContainer li::before{
    position: absolute;
    left: 9px;
    margin: 0 1rem 0 0;
    color: #b1b1b1;
    content: '\2022';
}

.InputContainer p{
    font-size: 12px;
}
.InputItem{
    padding: 0;
    position: relative;
    padding: 7px 0 6px 147px;
}

.InputItem .input-title{
    position: absolute;
    color: rgb(51,51,51);
    top: 7px;
    left: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 35px;
}

.must .input-title::after{
    position: absolute;
    top: 10px;
    right: -8px;
    width: 4px;
    height: 4px;
    border-radius: 2px;
    background-color: rgb(255,86,64);
    content: '';
}

.InputItem .input-element{
    font-size: 15px;
    color: rgb(102,102,102);
    padding-top: 7px;
}

.InputItem .input-element input{
    padding: 1px 11px 0;
    display: block;
    width: 100%;
    font-size: 12px;
    background: transparent;
    line-height: 18px;
    border: 1px solid rgb(216,216,216);
    border-radius: 2px;
    height: 28px;
    outline: 0 none;
    font-weight: 400;
    font-family: 'Noto Sans', sans-serif;
    color: rgb(51,51,51);
    resize: none;    
    word-wrap: break-word;
    word-break: break-all;
}

